import PropTypes       from 'prop-types';
import React           from 'react';
import { NavLink }     from 'react-router-dom';
import {
    findRouteByPath,
    normalizePath,
}                      from './helpers';
import getRoutesConfig from '../../Constants/routes_config';
import { connect }     from '../../../Stores/connect';

const useLanguageOn = (path, lang) => `/${lang.toLowerCase()}${path}`;
// TODO: solve circular dependency problem
// when binary link is imported into components present in routes config
// or into their descendants
const BinaryLink = ({ to, children, ...props }) => {
    const path          = useLanguageOn(normalizePath(to), props.current_language);
    const routes_config = getRoutesConfig(props.current_language.toLowerCase());
    const route         = findRouteByPath(path, routes_config);

    if (!route) {
        throw new Error(`Route not found: ${to}`);
    }

    return (
        to ?
            <NavLink to={path} activeClassName='active' exact={route.exact} {...props}>
                {children}
            </NavLink>
            :
            <a href='javascript:;' {...props}>
                {children}
            </a>
    );
};

BinaryLink.propTypes = {
    children: PropTypes.object,
    to      : PropTypes.string,
};

const binary_link = connect(({ common }) => ({
    current_language: common.current_language,
}))(BinaryLink);

export default binary_link;
