import {
    action,
    observable, runInAction,
} from 'mobx';
import moment              from 'moment';
import { currentLanguage } from 'Utils/Language/index';
import { urlFor }          from '_common/language';
import BaseStore           from './base_store';

export default class CommonStore extends BaseStore {
    @observable server_time      = moment.utc();
    @observable current_language = currentLanguage;
    @observable has_error        = false;

    @observable error = {
        type   : 'info',
        message: '',
    };

    @observable network_status    = {};
    @observable is_network_online = false;
    @observable is_socket_opened  = false;

    @observable languages = {};

    constructor() {
        super();
        this.bootLanguage();
    }

    @action.bound
    setIsSocketOpened(is_socket_opened) {
        this.is_socket_opened = is_socket_opened;
    }

    @action.bound
    setError(has_error, error) {
        this.has_error = has_error;
        this.error     = {
            type   : error ? error.type : 'info',
            message: error ? error.message : '',
        };
    }

    @action.bound
    showError(message) {
        this.setError(true, {
            message,
            type: 'error',
        });
    }

    @action.bound
    setLanguage(lang) {
        this.current_language = lang;
    }

    @action.bound
    async switchLanguage(lang) {
        await this.fetchNewLanguage(lang);
        this.fetchNewLanguage(lang);
        runInAction(() => {
            this.current_language = lang;
        });
        const body = document.querySelector('body');
        body.classList.remove(this.current_language.toUpperCase());
        body.classList.add(lang);
        history.pushState({}, 'Switching language', urlFor(lang));
    }

    @action.bound
    bootLanguage() {
        this.languages[currentLanguage] = texts_json[currentLanguage];
    }

    @action.bound
    fetchNewLanguage(lang) {
        return new Promise((resolve, reject) => {
            if (!this.languages[lang]) {
                const language = document.createElement('script');
                language.src   = `/app/js/texts/${lang.toLowerCase()}.js`;
                language.async = true;
                document.querySelector('body')
                    .appendChild(language);

                language.addEventListener('load', () => {
                    this.isLoaded = true;
                    resolve(language);
                });

                language.addEventListener('error', () => {
                    reject(new Error(`${this.src} failed to load.`));
                });
            }
            resolve({
                'message': 'Already loaded.',
            });
        });
    }

}
